.App .App-Header {
  display: block;
  text-align: center;
}

.App .recharts-wrapper {
  margin: auto;
}

.about {
  padding-left: 10%;
}

.footer {
  height: 80px;
}

.spacer {
  height: 40px;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px; height: 0; overflow: hidden;
}
 
.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.content {
  padding: 10px 10%;
}

